body {
  margin: 0;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

.container {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;

  width: 60vw;
}

.project-list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.project {
  margin: 0.2vw;
}

.toolbar {
  display: flex;
}

.btn-tool {
  height: 2vw;
  margin: 0.2vw;
  padding: 0.5vw;
  border: 1px solid #303030;
}
